export default{
	
	data(){
		return{
			messageList:[
				{
					author:'李明经理',
					messageContent:'N1节点需要修改下客户名称',
					createTime:'2017-10-26 13:00:05'
				},
				{
					author:'龙飞经理',
					messageContent:'N3节点附件需要更换',
					createTime:'2017-10-26 14:00:05'
				},
				{
					author:'孔维耀',
					messageContent:'N5节点的细节，需要周五早上商讨后再处理,N5处理人暂时先不要审批，感谢。',
					createTime:'2017-10-26 15:44:05'
				}
			],
			
			newMessageContent:""
			
		}
		
		
	},
	
	methods:{
		addMessage(){
			let newMessage = {
				author:'我',
				messageContent:this.newMessageContent,
				createTime:new Date().Format("yyyy-MM-dd hh:mm:ss")
			}
			
			this.messageList.push(newMessage);
			this.newMessageContent = "";
		}
	}
	
}
	



